"use client";
import extractErrorMessages from "@/src/app/utils/error.utils";
import { QuotationStatus } from "@/src/constants/enum";
import { QuoteService } from "@/src/services/quote.service";
import { QuotationUpdatePayload } from "@/src/types/quotations/quote.types";
import axios from "axios";
import { useParams } from "next/navigation";
import React, { JSX, useState, useEffect } from "react";
import { toast } from "react-toastify";
import Input from "@/src/components/ui/Input";
import Select from "@/src/components/ui/Select";

const page = (): JSX.Element => {
  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [err, setErr] = useState<string[] | string>("");
  const [formData, setFormData] = useState<QuotationUpdatePayload>({
    title: "",
    description: "",

    deal: "",

    customerName: "",
    customerEmail: "",
    customerPhone: null,

    companyName: null,

    issueDate: "",
    valid: null,

    subTotal: 0,
    total: 0,

    quotationStatus: "",
  });

  const { id } = useParams();

  const getQuotationData = async (id: string) => {
    try {
      const result = await QuoteService.get_by_id(id);

      if (result.status === 200) {
        const data = result.data.data;
        setFormData({});
      }
    } catch (error) {
      process.env.NEXT_PUBLIC_ENV === "development" && console.error(error);
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
        
      } else {
        setErr(["Something went wrong"]);
      }
    }
  };

  useEffect(() => {
    if (!id) return;

    getQuotationData(String(id));
  }, []);

  const handleUpdate = async () => {
    try {
    } catch (error) {
        process.env.NEXT_PUBLIC_ENV === "development" && console.error(error);
      if (axios.isAxiosError(error)) {
        const messages = extractErrorMessages(error);
        setErr(messages);
        toast.error(`${messages}`);
      } else {
        setErr(["Something went wrong"]);
      }
    }
  };

  return <div></div>;
};

export default page;
